
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  background-color:hsl(13, 31%, 94%);
  font-family: "Red Hat Text", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  padding: 30px 0 40px;
}

.uninteractive:not(.third-child) {
  pointer-events: none;
  user-select: none; 
}
.error-message{
  margin-top: 15px;
  display: inline-block;
}

.spandiv{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.span-1{
  font-size: 13px;
  font-weight: 200;
}
.span-2{
  font-weight: 700;
  font-size: 18px;
}
.thirdchild .span-2{
  display: flex;
  justify-content: right;
}


.container{
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: auto;
  margin: 10px 60px;
  position: relative;
}
.firstchild h1{
  font-size: 32px;
}
.thirdchild{
  display: grid;
  grid-column: 1/3;
  background-color: white;
  width: 40%;
  margin: auto;
  height: auto;
  padding: 40px 25px;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translate(-50%,-50%);
  pointer-events: auto; 
  z-index: 10;
}
.thirdchild .spandiv{
  padding-right: 20px;
}
.confirm-order-2{
  margin-bottom: 20px;
}
.confirm-order-2 h1{
  margin-top: 10px 0 5px;
}
.confirm-order-2 h5{
  font-weight: 400;
  font-size: smaller;
  color:hsl(14, 25%, 72%);
}
.confirm-order-3{
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  grid-template-rows:auto ;
  width: 100%;
  align-items: center;
  margin-bottom: 10px;
}
.confirm-order-3 .three-i img{
   width: 60px;
   height: auto;
   border-radius: 5px;
}
.confirm-order-3 .three-ii{
  margin-left: 10px;
}
.three-ii p:nth-child(1){
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 2%;
}
.confirm-order-3 .three-iii{
 font-weight: 900;
 font-size: 16px;
}
.three-ii p:nth-child(2) span:nth-child(1){
  color: hsl(14, 86%, 42%);
  font-weight: 700;
  font-size: small;
  margin-right: 5%;
}
.three-ii p:nth-child(2) span:nth-child(2){
  font-weight:700 ;
  color: hsl(14, 25%, 72%);
  font-size: small;
}
.innerfirstchild{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
}
.innerfirstchild img{
  width: auto;
  height: 255px;
  border-radius: 5%;
  margin-bottom: 7%;
  aspect-ratio: 1/1;
  object-fit: cover;
}
.innerfirstchild img:active{
  border: 2px solid hsl(14, 86%, 42%);
}
.innerfirstchild h4{
  color:rgba(0,0,0,0.2);
  font-size: 0.8rem;
}
.innerfirstchild p{
  color: hsl(14, 86%, 42%);
}
.innerfirstchild h2{
  font-size: 1.2rem;
}
.innerfirstchild p{
  font-size: 1rem;
  font-weight: 700;
}
.firstchild h1{
  margin:  20px 0 40px;
  font-weight: 700;
}
.img{
  position: relative;
  margin-bottom: 10px;
}
.cart-button{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
  left:45%;
  transform: translate(-50%, -10%);
  border: 0;
  padding: 6px;
  border-radius: 5px;
}
.mycart-btn{
  width: 100%;
  background-color: hsl(14, 86%, 42%);
  border: 0;
  border-radius: 20px;
  padding: 15px;
  color: white;
  font-size: 14px;
  cursor:pointer;
  margin: 10px 0;
}
.secondchild{
  margin: 20px 0;
  padding: 10px 8px;
}
.secondchild .divbox{
  background-color: white;
  border-radius: 10px;
  height: auto;
  /* background-color: green; */
  padding: 20px;
}
.divbox h2{
  font-size: 22px;
  font-weight: 700;
  color: hsl(14, 86%, 42%);
  margin: 10px 0 20px;
}
.cart-itemdiv{
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 5px;
}
.cart-itemdiv div{
  font-weight: 200;
}
.cart-itemdiv p{
  color:hsl(15, 11%, 71%) ;
  font-size: 12px;
}
.f-flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.f-flex span{
  cursor: pointer;
  border: 1px solid hsl(15, 11%, 71%);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.fa-shop{
  color: hsl(14, 86%, 42%);
  
}
.neutral-green button{
  font-size: 12px;
  border: 0;
  width: 100%;
  color: hsl(15, 11%, 71%);
  border-radius: 5px;
  padding: 9px;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.div-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
  text-align: center;
  font-size: 13px;
  color: hsl(15, 11%, 71%);
}
.cart-button{
  color: black;
  border: 1px solid black;
  padding: 5px 15px;
  border-radius: 15px;
  cursor: pointer;
}
.cart-button:hover{
  color: hsl(14, 86%, 42%);
  border: 1px solid hsl(14, 86%, 42%);
  padding: 5px 15px;
  border-radius: 15px;
  cursor: pointer;
}

