.btns{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
  left:45%;
  background-color: hsl(14, 86%, 42%);
  transform: translate(-50%, -10%);
  border: 0;
  padding: 5px 15px;
  border-radius: 15px;
  color: white;
  height: 39px;
  width: 123.44px;
}
.btns button{
  display: inline-flex;
  justify-content:center;
  align-items: center;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  background-color: hsl(14, 86%, 42%);
  border: 1px solid white;
  color: white;
  cursor: pointer;
  font-size: medium;
  font-weight: 500;
  padding: 0;
  text-align: center;
}
.btns button:hover{
  background-color: white;
  color: hsl(14, 86%, 42%);
}
.s-flex{
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  width: 45%;
  font-size: 16px;
}

.s-flex p:nth-child(1){
  color: hsl(14, 86%, 42%);
  font-weight: 500;
  padding-right: 10px;
}
.s-flex p:nth-child(3){
  padding-left: 10px;
  font-weight: 500;
}

@media (max-width: 480px) {
  .container{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    margin: 10px 20px;
  }
  .innerfirstchild{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }
  .innerfirstchild img{
    width: 100%;
    height: 250px;
    border-radius: 5%;
  }
  .cart-button{
    position: absolute;
    bottom: 0;
    left:50%;
    transform: translate(-50%, -10%);
  }
  .btns{
    left: 50%;
  }
  .neutral-green button{
    font-size: 10px;
  }
  .thirdchild{
    width: 100%;
    top: 8%;
  }
  .thirdchild .span-2{
    margin-right: -20px;
  }
}